body *, body *::before, body *::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body a {
  text-decoration: none;
  display: block;
}

body img {
  vertical-align: middle;
}

.navList {
  right: 0;
  top: 50%;
}

.confirmNumber {
  right: -12px;
  top: -7px;
  color: white;
  background: #E31277;
  border-radius: 9px;
  width: 22px;
  height: 18px;
}

.mainTitleStyle {
  color: black;
  font-weight: bold;
  font-size: 32px;
}

.banner {
  background-image: url("https://images.unsplash.com/photo-1506846540181-347008948fff?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1186&q=80");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  width: 100%;
  height: 480px;
}

.bannerBtnGroup button {
  width: 160px;
  height: 44px;
}

.subNavListItem a {
  color: #707070;
  padding: 14px 28px;
}

.card img {
  object-fit: cover;
}
